import React, {Component} from 'react'
import {
  Table,
  Button,
  Container,
  Modal,
  ModalHeader,
  ModalBody,
  FormGroup,
  ModalFooter,
} from "reactstrap";


const data = [
    {id: 1, mascota: "Perro", cliente: "Dueño1"},
    {id: 2, mascota: "Gato", cliente: "Dueño1"},
    {id: 3, mascota: "CatDog", cliente: "Dueño1"},
    {id: 4, mascota: "Perro Otave", cliente: "Dueño1"},
    {id: 5, mascota: "Gatotave", cliente: "Dueño1"},
    {id: 6, mascota: "Nyaaa~", cliente: "Dueño1"},
  ];

export class TableComponent extends Component {
    state = {
        data: data,
        form: {
            id: '',
            mascota: '',
            cliente: '',
        },
        modalInsert: false,
        modalEdit: false,
    };

    handleChange = e => {
        this.setState({
            form: {
                ...this.state.form,
                [e.target.name]: e.target.value,
            }
        });
    }

    showAddModal = () => {
        this.setState({modalInsert: true});
    }

    hideAddModal = () => {
        this.setState({modalInsert: false});
    }

    showEditModal = (registro) => {
        this.setState({modalEdit: true, form: registro});
    }

    hideEditModal = () => {
        this.setState({modalEdit: false});
    }

    insertar = () => {
        var idNueva = {...this.state.form};
        idNueva.id = this.state.data.length+1;
        var lista = this.state.data;
        lista.push(idNueva)
        this.setState({data: lista, modalInsert: false})

    }

    guardar = (dato) => {
        var contador=0;
        var lista=this.state.data;
        lista.map((registro) => {
            if (dato.id == registro.id) {
                lista[contador].mascota = dato.mascota;
                lista[contador].cliente = dato.cliente;
            }
            contador++;
        })
        this.setState({data: lista, modalEdit: false});
        
    }

    eliminar = (dato) => {
        var opcion = window.confirm("Realmente deseas eliminar " + dato.mascota + "?");
        if (opcion) {
            var contador=0;
            var lista = this.state.data;
            lista.map((registro) => {
                if (dato.id ==registro.id ) {
                    lista.splice(contador, 1)
                }
                contador++;
            });
            this.setState({data: lista})
        } 
    }
    render () {
        return (
            <div>
            <Container>
                <br/>
                <Button color="success" onClick={ () => this.showAddModal() }><ion-icon name="add"></ion-icon> Nuevo</Button>   
                <br/> <br/>

                <Table>
                    <thead>
                        <tr>
                            <th>ID</th>
                            <th>mascota</th>
                            <th>cliente</th>
                            <th>Acciones</th>
                        </tr>
                    </thead>

                    <tbody>
                        { this.state.data.map( (datitos) => (
                        <tr>
                            <td>{datitos.id}</td>
                            <td>{datitos.mascota}</td>
                            <td>{datitos.cliente}</td>
                            <td>
                                <Button color="info" onClick={ () => this.showEditModal(datitos) } >Editar <ion-icon name="pencil"></ion-icon></Button>
                                {"  "}
                                <Button color="danger" onClick={ () => this.eliminar(datitos) } >Borrar <ion-icon name="trash-bin"></ion-icon> </Button>
                            </td>
                        </tr>
                        ))}
                    </tbody>

                </Table>

            </Container>  

            <Modal isOpen={this.state.modalInsert}>

                <ModalHeader>
                    <div>
                        <h1>Nueva mascota Kawaii Desune</h1>
                    </div>
                </ModalHeader>

                <ModalBody>

                    <FormGroup>
                        <label>ID: </label>
                        <input className="form-control" readOnly type="text" value={this.state.data.length+1}/>
                    </FormGroup>

                    <FormGroup>
                        <label>mascota </label>
                        <input className="form-control" name="mascota" type="text" onChange={this.handleChange} />
                    </FormGroup>

                    <FormGroup>
                        <label>dueño </label>
                        <input className="form-control" name="dueño" type="text"onChange={this.handleChange} />
                    </FormGroup>

                </ModalBody>

                <ModalFooter>
                    <Button color="success" onClick={ () => this.insertar() }> Guardar <ion-icon name="checkmark"></ion-icon></Button>
                        {"  "}
                    <Button color="danger" onClick={ () => this.hideAddModal() }><ion-icon name="close"></ion-icon> Cancelar </Button>
                </ModalFooter>

                </Modal>  


                <Modal isOpen={this.state.modalEdit}>

                <ModalHeader>
                    <div>
                        <h5>Editar datos para el/la: {this.state.form.mascota} de {this.state.form.cliente}</h5>
                    </div>
                </ModalHeader>

                <ModalBody>

                    <FormGroup>
                        <label>ID: </label>
                        <input className="form-control" readOnly type="text" value={this.state.form.id}/>
                    </FormGroup>

                    <FormGroup>
                        <label>mascota </label>
                        <input className="form-control" name="mascota" type="text" onChange={this.handleChange} value={this.state.form.mascota} />
                    </FormGroup>

                    <FormGroup>
                        <label>dueño </label>
                        <input className="form-control" name="dueño" type="text"onChange={this.handleChange} value={this.state.form.dueño} />
                    </FormGroup>

                </ModalBody>

                <ModalFooter>
                    <Button color="success" onClick={ () => this.guardar(this.state.form) }> Confirmar <ion-icon name="checkmark"></ion-icon></Button>
                        {"  "}
                    <Button color="danger" onClick={ () => this.hideEditModal() }><ion-icon name="close"></ion-icon> Cancelar </Button>
                </ModalFooter>

                </Modal>  
        </div>
        )
    }
}

export default TableComponent